import { ScreenBounds } from "../common/screen-bounds";
import { ScreenXY } from "../common/screen-xy";
/**
 * 
 * @remarks
 * 
 * Canvas
 */
export class Symbol {
    constructor() {
        /**
         * 
         */
        this.lineWidth = 2;
        this.stroke = true;
        /**
         * 
         */
        this.strokeStyle = "#ff0000";
        this.strokeColor = "#ff0000";
        this.strokeOpacity = 1;
        this.fill = true;
        /**
         * 
         */
        this.fillStyle = "#ff000088";
        this.fillColor = "#ff0000";
        this.fillOpacity = 0.5;
        // abstract contain(screenXY: ScreenXY): boolean;
    }
    get weight() {
        return this.lineWidth;
    }
    set weight(value) {
        this.lineWidth = value;
    }
}
/**
 * 
 */
export class PointSymbol extends Symbol {
}
/**
 * 
 */
export class LineSymbol extends Symbol {
    constructor() {
        super(...arguments);
        this.lineWidth = 3;
    }
}
/**
* 
* @remarks
* aka 
*/
export class FillSymbol extends Symbol {
}
/**
 * 
 * @remarks
 * 
 */
export class SimplePointSymbol extends PointSymbol {
    constructor() {
        super(...arguments);
        /**
         * 
         */
        this.radius = 10;
    }
    /**
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {number} screenX - X
     * @param {number} screenY - Y
     */
    draw(ctx, screenXY) {
        //ctx.save();
        ctx.strokeStyle = this.strokeStyle;
        ctx.fillStyle = this.fillStyle;
        ctx.lineWidth = this.lineWidth;
        ctx.beginPath(); //Start path
        ctx.arc(screenXY.x, screenXY.y, this.radius, 0, Math.PI * 2, false);
        // ctx.globalAlpha = this.fillOpacity;
        // ctx.fillStyle = this.fillColor;
        ctx.fill('evenodd');
        // ctx.globalAlpha = this.strokeOpacity;
        // ctx.strokeStyle = this.strokeColor;
        // ctx.lineWidth = this.lineWidth;
        ctx.stroke();
        //ctx.restore();
    }
    getScreenBounds(screenXY) {
        let r = this.radius, w = this.stroke ? this.weight / 2 : 0, p = new ScreenXY(r + w, r + w);
        return new ScreenBounds(screenXY.subtract(p), screenXY.add(p));
    }
}
/**
 * 
 * @remarks
 * POI
 */
export class SimpleMarkerSymbol extends PointSymbol {
    constructor() {
        super(...arguments);
        /**
         * 
         */
        this.width = 16;
        /**
         * 
         */
        this.height = 16;
        /**
         * offset
         * 16px16pxoffsetX-8offsetY-8
         * 
         */
        this.offsetX = -8;
        /**
         * offset
         * 16px16pxoffsetX-8offsetY-8
         * 
         */
        this.offsetY = -8;
    }
    /**
     * 
     */
    get loaded() {
        return this._loaded;
    }
    /**
     * 
     * @return {Color} 
     */
    load() {
        return new Promise((resolve, reject) => {
            let img = new Image();
            img.onload = () => {
                createImageBitmap(img).then(icon => {
                    this.image = icon;
                    this._loaded = true;
                    resolve(icon);
                }, err => reject(err));
            };
            img.onerror = reject;
            img.src = this.url;
        });
    }
    /**
     * 
     * @remarks
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {number} screenX - X
     * @param {number} screenY - Y
     */
    draw(ctx, screenXY) {
        // if (!this._loaded) {
        //     this.image = new Image();
        //     this.image.src = this.url;
        //     this.image.onload = () => {
        //       ctx.drawImage(this.image, screenXY.x + this.offsetX, screenXY.y + this.offsetY, this.width, this.height);
        //     }
        //     this._loaded = true;
        // } else {
        //   if (!this._loading) {
        //     ctx.drawImage(this.image, screenXY.x + this.offsetX, screenXY.y + this.offsetY, this.width, this.height);
        //   }
        // }
        // find a better way
        // now we need await image load
        if (!this._loaded) {
            this.image = new Image();
            this.image.src = this.url;
            this._loaded = true;
        }
        else {
            ctx.drawImage(this.image, screenXY.x + this.offsetX, screenXY.y + this.offsetY, this.width, this.height);
        }
    }
    getScreenBounds(screenXY) {
        let p1 = new ScreenXY(this.offsetX, this.offsetY);
        let p2 = new ScreenXY(this.offsetX + this.width, this.offsetY + this.height);
        return new ScreenBounds(screenXY.add(p1), screenXY.add(p2));
    }
}
/**
 * 
 * @remarks
 * 
 */
export class SimpleLineSymbol extends LineSymbol {
    /**
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {number[][]} screen - 
     */
    draw(ctx, screenXYs) {
        if (screenXYs.length < 2)
            return;
        // ctx.save();
        ctx.strokeStyle = this.strokeStyle;
        ctx.lineWidth = this.lineWidth;
        ctx.beginPath();
        screenXYs.forEach((screenXY, index) => {
            if (index === 0) {
                ctx.moveTo(screenXY.x, screenXY.y);
            }
            else {
                ctx.lineTo(screenXY.x, screenXY.y);
            }
        });
        ctx.stroke();
        // ctx.restore();
    }
    getScreenBounds(screenXYs) {
        const bounds = new ScreenBounds();
        screenXYs.forEach(screenXY => {
            bounds.extend(screenXY);
        });
        // extend weight
        return bounds;
    }
}
/**
 * 
 * @remarks
 * 
 */
export class SimpleFillSymbol extends FillSymbol {
    /**
     * 
     * @remarks
     * 
     * https://developer.mozilla.org/zh-CN/docs/Web/API/CanvasRenderingContext2D/fill
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {number[][][]} screen - 
     */
    draw(ctx, screenXYs) {
        // ctx.save();
        ctx.strokeStyle = this.strokeStyle;
        ctx.fillStyle = this.fillStyle;
        ctx.lineWidth = this.lineWidth;
        //TODO:  exceeding the maximum extent(bound), best way is overlap by extent. find out: maximum is [-PI*R, PI*R]??
        ctx.beginPath();
        screenXYs.forEach(ring => {
            if (ring.length < 3)
                return;
            ring.forEach((screenXY, index) => {
                if (index === 0) {
                    ctx.moveTo(screenXY.x, screenXY.y);
                }
                else {
                    ctx.lineTo(screenXY.x, screenXY.y);
                }
            });
        });
        ctx.closePath();
        //
        //https://developer.mozilla.org/zh-CN/docs/Web/API/CanvasRenderingContext2D/fill
        ctx.fill("evenodd");
        ctx.stroke();
        // ctx.restore();
    }
    getScreenBounds(screenXYs) {
        const bounds = new ScreenBounds();
        screenXYs.forEach(ring => {
            ring.forEach(screenXY => {
                bounds.extend(screenXY);
            });
        });
        // extend weight
        return bounds;
    }
}
