import * as Util from '../util/util';
import { Earth } from '../crs/crs-earth';
import { LatLngBounds } from './latlng-bounds';
/**
 * 
 */
export class LatLng {
    /**
      * 
      * @param {number} lat - 
      * @param {number} lng - 
      * @param {number} alt - 
      */
    constructor(lat = 0, lng = 0, alt = 0) {
        // @property lat: Number
        // Latitude in degrees
        this.lat = lat;
        // @property lng: Number
        // Longitude in degrees
        this.lng = lng;
        // @property alt: Number
        // Altitude in meters (optional)
        this.alt = alt;
    }
    // @method equals(otherLatLng: LatLng, maxMargin?: Number): Boolean
    // Returns `true` if the given `LatLng` point is at the same position (within a small margin of error). The margin of error can be overridden by setting `maxMargin` to a small number.
    /**
   * 
   * @param {LatLng} obj - 
   * @param {number} maxMargin - 
   * @return {boolean} 
   */
    equals(obj, maxMargin = 1.0E-9) {
        if (!obj) {
            return false;
        }
        const margin = Math.max(Math.abs(this.lat - obj.lat), Math.abs(this.lng - obj.lng));
        return margin <= maxMargin;
    }
    // @method toString(): String
    // Returns a string representation of the point (for debugging purposes).
    /**
     * 
     * @param {number} precision - 
     * @return {string} 
     */
    toString(precision = 6) {
        return 'LatLng(' +
            Util.formatNum(this.lat, precision) + ', ' +
            Util.formatNum(this.lng, precision) + ')';
    }
    /**
     * GeoJSON
     * @param {number} precision - 
     * @return {Object} GeoJSON
     */
    toGeoJSON(precision = 6) {
        return [Util.formatNum(this.lng, precision), Util.formatNum(this.lat, precision)];
    }
    // @method distanceTo(otherLatLng: LatLng): Number
    // Returns the distance (in meters) to the given `LatLng` calculated using the [Spherical Law of Cosines](https://en.wikipedia.org/wiki/Spherical_law_of_cosines).
    /**
   * 
   * @param {LatLng} other - 
   * @return {number} 
   */
    distanceTo(other) {
        return new Earth().distance(this, other);
    }
    // @method wrap(): LatLng
    // Returns a new `LatLng` object with the longitude wrapped so it's always between -180 and +180 degrees.
    /**
   * 
   * @return {LatLng} 
   */
    wrap() {
        return new Earth().wrapLatLng(this);
    }
    // @method toBounds(sizeInMeters: Number): LatLngBounds
    // Returns a new `LatLngBounds` object in which each boundary is `sizeInMeters/2` meters apart from the `LatLng`.
    /**
   * 
   * @param {number} sizeInMeters - 
   * @return {LatLng} 
   */
    toLatLngBounds(sizeInMeters) {
        var latAccuracy = 180 * sizeInMeters / 40075017, lngAccuracy = latAccuracy / Math.cos((Math.PI / 180) * this.lat);
        return new LatLngBounds(new LatLng(this.lat - latAccuracy, this.lng - lngAccuracy), new LatLng(this.lat + latAccuracy, this.lng + lngAccuracy));
    }
    /**
     * 
     * @return {LatLng} 
     */
    clone() {
        return new LatLng(this.lat, this.lng, this.alt);
    }
}
