import { ScreenBounds } from "../common/screen-bounds";
import { LineSymbol } from "./symbol";
/**
 * 
 * @remarks
 * 
 */
export class AlternateLineSymbol extends LineSymbol {
    constructor() {
        super(...arguments);
        this.color1 = "#000000";
        this.color2 = "#ffffff";
        this.alternate = 10;
    }
    /**
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {number[][]} screen - 
     */
    draw(ctx, screenXYs) {
        if (screenXYs.length < 2)
            return;
        ctx.save();
        ctx.strokeStyle = this.color1;
        ctx.lineWidth = this.lineWidth;
        ctx.setLineDash([this.alternate, this.alternate]);
        ctx.beginPath();
        screenXYs.forEach((screenXY, index) => {
            if (index === 0) {
                ctx.moveTo(screenXY.x, screenXY.y);
            }
            else {
                ctx.lineTo(screenXY.x, screenXY.y);
            }
        });
        ctx.stroke();
        ctx.strokeStyle = this.color2;
        ctx.lineDashOffset = this.alternate;
        ctx.beginPath();
        screenXYs.forEach((screenXY, index) => {
            if (index === 0) {
                ctx.moveTo(screenXY.x, screenXY.y);
            }
            else {
                ctx.lineTo(screenXY.x, screenXY.y);
            }
        });
        ctx.stroke();
        ctx.restore();
    }
    getScreenBounds(screenXYs) {
        const bounds = new ScreenBounds();
        screenXYs.forEach(screenXY => {
            bounds.extend(screenXY);
        });
        // extend weight
        return bounds;
    }
}
