import { formatNum } from '../util/util';
/**
 * 
 */
export class XY {
    /**
      * 
      * @param {number} x - X
      * @param {number} y - Y
      */
    constructor(x = 0, y = 0) {
        // @property x: Number; The `x` coordinate of the point
        this.x = x;
        // @property y: Number; The `y` coordinate of the point
        this.y = y;
    }
    // @method add(otherPoint: Point): Point
    // Returns the result of addition of the current and the given points.
    /**
   * 
   * @param {XY} otherXY - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    add(otherXY, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x += otherXY.x;
        source.y += otherXY.y;
        return source;
    }
    // @method subtract(otherPoint: Point): Point
    // Returns the result of subtraction of the given point from the current.
    /**
   * 
   * @param {XY} otherXY - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    subtract(otherXY, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x -= otherXY.x;
        source.y -= otherXY.y;
        return source;
    }
    // @method divideBy(num: Number): Point
    // Returns the result of division of the current point by the given number.
    /**
   * 
   * @param {number} num - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    divideBy(num, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x /= num;
        source.y /= num;
        return source;
    }
    // @method multiplyBy(num: Number): Point
    // Returns the result of multiplication of the current point by the given number.
    /**
   * 
   * @param {number} num - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    multiplyBy(num, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x *= num;
        source.y *= num;
        return source;
    }
    // @method scaleBy(scale: Point): Point
    // Multiply each coordinate of the current point by each coordinate of
    // `scale`. In linear algebra terms, multiply the point by the
    // [scaling matrix](https://en.wikipedia.org/wiki/Scaling_%28geometry%29#Matrix_representation)
    // defined by `scale`.
    /**
   * 
   * @param {XY} otherXY - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    scaleBy(otherXY, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x *= otherXY.x;
        source.y *= otherXY.y;
        return source;
    }
    // @method unscaleBy(scale: Point): Point
    // Inverse of `scaleBy`. Divide each coordinate of the current point by
    // each coordinate of `scale`.
    /**
   * 
   * @param {XY} otherXY - 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    unscaleBy(otherXY, clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x /= otherXY.x;
        source.y /= otherXY.y;
        return source;
    }
    // @method round(): Point
    // Returns a copy of the current point with rounded coordinates.
    /**
   * 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    round(clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x = Math.round(this.x);
        source.y = Math.round(this.y);
        return source;
    }
    // @method floor(): Point
    // Returns a copy of the current point with floored coordinates (rounded down).
    /**
   * 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    floor(clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x = Math.floor(this.x);
        source.y = Math.floor(this.y);
        return source;
    }
    // @method ceil(): Point
    // Returns a copy of the current point with ceiled coordinates (rounded up).
    /**
   * 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    ceil(clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x = Math.ceil(this.x);
        source.y = Math.ceil(this.y);
        return source;
    }
    // @method trunc(): Point
    // Returns a copy of the current point with truncated coordinates (rounded towards zero).
    /**
   * 
   * @param {boolean} clone - 
   * @return {XY} 
   */
    trunc(clone = true) {
        let source = this;
        // non-destructive, returns a new point
        if (clone) {
            source = this.clone();
        }
        source.x = Math.trunc(this.x);
        source.y = Math.trunc(this.y);
        return source;
    }
    // @method distanceTo(otherPoint: Point): Number
    // Returns the cartesian distance between the current and the given points.
    /**
   * 
   * @param {XY} point - 
   * @return {number} 
   */
    distanceTo(point) {
        let x = point.x - this.x, y = point.y - this.y;
        return Math.sqrt(x * x + y * y);
    }
    // @method equals(otherPoint: Point): Boolean
    // Returns `true` if the given point has the same coordinates.
    /**
   * 
   * @param {XY} obj - 
   * @return {boolean} 
   */
    equals(point) {
        if (!point)
            return false;
        return point.x === this.x &&
            point.y === this.y;
    }
    // @method contains(otherPoint: Point): Boolean
    // Returns `true` if both coordinates of the given point are less than the corresponding current point coordinates (in absolute values).
    /**
   * 
   * @remark Returns `true` if both coordinates of the given point are less than the corresponding current point coordinates (in absolute values).
   * @param {XY} obj - 
   * @return {boolean} 
   */
    contains(point) {
        return Math.abs(point.x) <= Math.abs(this.x) &&
            Math.abs(point.y) <= Math.abs(this.y);
    }
    // @method toString(): String
    // Returns a string representation of the point for debugging purposes.
    /**
   * 
   * @param {number} precision - 
   * @return {string} 
   */
    toString(precision = 3) {
        return 'XY(' +
            formatNum(this.x, precision) + ', ' +
            formatNum(this.y, precision) + ')';
    }
}
