import { EventedObject } from "../base/evented-object";
export class Graphic extends EventedObject {
    constructor(geometry, symbol) {
        super();
        this.prev = null;
        this.next = null;
        this._geometry = geometry;
        this._symbol = symbol;
    }
    get geometry() {
        return this._geometry;
    }
    get symbol() {
        return this._symbol;
    }
    transform(origin, zoom) {
        this._geometry.transform(origin, zoom, this._symbol);
    }
    /**
       * 
       * @param {CanvasRenderingContext2D} ctx - 
       * @param {Projection} projection - 
       * @param {Bound} extent - 
       * @param {Symbol} symbol - 
       */
    draw(ctx) {
        this._geometry.draw(ctx, this._symbol);
    }
}
