import { BaseObject } from "./base-object";
/**
 * 
 */
export class OptionsObject extends BaseObject {
    /**
      * 
      */
    constructor() {
        super();
    }
    /**
      * 
      */
    assign(options) {
        if (options !== undefined) {
            for (let key in options) {
                if (this.hasOwnProperty(key)) {
                    this[key] = options[key];
                }
            }
        }
    }
}
