export class Collision {
    /**
     * 
     * @param {Feature[]} features - 
     * @param {Field} field - 
     * @param {SimpleTextSymbol} symbol - 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {Projection} projection - 
     * @return {Feature[]} 
     */
    test(ctx, features, field, text) { return []; }
}
/**
 * 
 */
export class NullCollision {
    /**
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {Feature[]} features - 
     * @param {Field} field - 
     * @param {Text} text - 
     * @return {Feature[]} 
     */
    test(ctx, features, field, text) {
        //
        return features;
    }
}
/**
 * 
 * @remarks
 * 
 */
export class SimpleCollision {
    constructor() {
        /**
         * 
         * @remarks
         *  pixel
         */
        this.distance = 50;
    }
    /**
     * 
     * @param {CanvasRenderingContext2D} ctx - 
     * @param {Feature[]} features - 
     * @param {Field} field - 
     * @param {Text} text - 
     * @return {Feature[]} 
     */
    test(ctx, features, field, text) {
        //
        return features.reduce((acc, cur) => {
            const item = acc.find((item) => {
                const distance = cur.geometry.distance(item.geometry, CoordinateType.Screen);
                return distance <= this.distance;
            });
            if (!item)
                acc.push(cur);
            return acc;
        }, []); // [feature]
    }
}
