import { EventedObject } from "../base/evented-object";
import { Text } from "../text/text";
export class Feature extends EventedObject {
    /**
      * 
      * @param {Geometry} geometry - 
      * @param {Object} properties - 
      */
    constructor(geometry, properties) {
        super();
        /**
         * 
         */
        this._properties = {};
        /**
         * 
         */
        this.visible = true;
        /**
         * 
         * @remarks
         * FeatureClass
         */
        this.prev = null;
        /**
         * 
         * @remarks
         * FeatureClass
         */
        this.next = null;
        this._geometry = geometry;
        this._properties = properties;
    }
    /**
     * 
     */
    get geometry() {
        return this._geometry;
    }
    /**
     * 
     */
    get properties() {
        return this._properties;
    }
    /**
      * 
      * @param {CanvasRenderingContext2D} ctx - 
      * @param {Symbol} symbol - 
      */
    draw(ctx, symbol) {
        this._geometry.draw(ctx, symbol);
    }
    /**
      * 
      * @remarks 
      * @param {CanvasRenderingContext2D} ctx - 
      * @param {Field} field - 
      * @param {Text} text - 
      */
    label(ctx, field, text = new Text()) {
        if (this.visible)
            this._geometry.label(ctx, this._properties[field.name], this.text || text);
    }
}
