import { LatLng } from "../common/latlng";
import { LatLngBounds } from "../common/latlng-bounds";
/*
 * 
 */
export class Raster {
    /**
     * 
     * @remarks
     * 
     * @param {number} xmin - 
     * @param {number} ymin - 
     * @param {number} xmax - 
     * @param {number} ymax - 
     * @param {number} width - 
     * @param {number} height - 
     * @param {number} cellsize - 
     */
    constructor(xmin, ymin, xmax, ymax, width = 1000, height = 1000) {
        this._canvas = document.createElement("canvas");
        this._canvas.width = width;
        this._canvas.height = height;
        this._latlngBounds = new LatLngBounds(new LatLng(ymin, xmin), new LatLng(ymax, xmax));
    }
    /*
     * 
     */
    get dynamic() {
        return false;
    }
    /*
     * Image
     */
    get canvas() {
        return this._canvas;
    }
    /*
     * 
     */
    get bounds() {
        return this._latlngBounds;
    }
    /**
     * 
     * @remarks
     * bound
     */
    get latlngBounds() {
        return this._latlngBounds;
    }
    get planeBounds() {
        return this._planeBounds;
    }
    get screenBounds() {
        return this._screenBounds;
    }
    set crs(value) {
        this._crs = value;
        this.project();
    }
}
