import { SimpleFillSymbol, SimpleLineSymbol, SimplePointSymbol } from "../symbol/symbol";
import { GeometryType } from "../geometry/geometry";
import { Color } from "../util/color";
import { Renderer } from "./renderer";
/**
 * 
 */
export class CategoryRendererItem {
    constructor(value, symbol, label) {
        /**
         * 
         */
        this.count = 1;
        this.value = value;
        this.symbol = symbol;
        this.label = label;
    }
}
/**
 * 
 * @remarks
 * generate
 * items.push()
 */
export class CategoryRenderer extends Renderer {
    constructor() {
        super(...arguments);
        /**
         * 
         */
        this._items = [];
    }
    /**
     * 
     * @remarks
     * 
     */
    get field() {
        return this._field;
    }
    set field(value) {
        this._field = value;
    }
    /**
     * 
     */
    get items() {
        return this._items;
    }
    /**
     * 
     * @param {FeatureClass} featureClass - 
     * @param {Field} field - 
     */
    generate(featureClass, field) {
        this._field = field;
        this._items = [];
        //
        featureClass.features.map(feature => feature.properties[field.name]).forEach((value) => {
            const item = this._items.find(item => item.value == value);
            if (item) {
                item.count += 1;
            }
            else {
                const item = new CategoryRendererItem();
                switch (featureClass.type) {
                    case GeometryType.Point:
                        // case GeometryType.MultiplePoint:
                        const symbol1 = new SimplePointSymbol();
                        symbol1.fillStyle = Color.random().toString();
                        symbol1.strokeStyle = Color.random().toString();
                        item.symbol = symbol1;
                        item.value = value;
                        this._items.push(item);
                        break;
                    case GeometryType.Polyline:
                        // case GeometryType.MultiplePolyline:
                        const symbol2 = new SimpleLineSymbol();
                        symbol2.strokeStyle = Color.random().toString();
                        item.symbol = symbol2;
                        item.value = value;
                        this._items.push(item);
                        break;
                    case GeometryType.Polygon:
                        // case GeometryType.MultiplePolygon:
                        const symbol3 = new SimpleFillSymbol();
                        symbol3.fillStyle = Color.random().toString();
                        symbol3.strokeStyle = Color.random().toString();
                        item.symbol = symbol3;
                        item.value = value;
                        this._items.push(item);
                        break;
                }
            }
        });
    }
    getSymbol(feature) {
        const item = this.items.find(item => item.value == feature.properties[this.field.name]);
        if (item)
            return item.symbol;
    }
}
