import { EventedObject } from "../base/evented-object";
/**
 * 
 */
export class Layer extends EventedObject {
    /**
     * 
     */
    constructor() {
        super();
        /**
         * 
         */
        this._visible = true;
        /**
         * 
         */
        this._zoom = [1, 20];
        /**
         * 
         */
        this._interactive = true;
        /**
         * z-index
         * @remarks
         * TODO: marker
         */
        this._index = 0; //z-index
    }
    /**
     * 
     */
    get visible() {
        return this._visible;
    }
    /**
     * 
     */
    set visible(value) {
        this._visible = value;
    }
    /**
       * 
       */
    get minZoom() {
        return this._zoom[0];
    }
    get maxZoom() {
        return this._zoom[1];
    }
    set minZoom(value) {
        this._zoom[0] = value;
    }
    set maxZoom(value) {
        this._zoom[1] = value;
    }
    set zoom(value) {
        this._zoom = value;
    }
    /**
     * 
     */
    get interactive() {
        return this._interactive;
    }
    /**
     * 
     */
    set interactive(value) {
        this._interactive = value;
    }
    /**
     * 
     */
    get index() {
        return this._index;
    }
    /**
     * 
     */
    set index(value) {
        this._index = value;
    }
}
