/**
 * 
 * @remarks
 * TODO: a lot of things to be done
 * CRSFeatureLayerMap
 */
export class FeatureClass {
    /**
     * 
     * @param {GeometryType} type - //
     */
    constructor(type) {
        /**
         * 
         */
        this._fields = [];
        /**
         * 
         */
        this._features = {}; //Map<string, Graphic>
        this._type = type;
    }
    /**
     * 
     */
    get first() {
        return this._first;
    }
    /**
     * 
     */
    get last() {
        return this._last;
    }
    /**
     * //
     */
    get type() {
        return this._type;
    }
    /**
     * 
     */
    get features() {
        return Object.values(this._features);
    }
    /**
     * 
     */
    get fields() {
        return this._fields;
    }
    /**
     * ID
     * @param {string} id - ID
     */
    getFeature(id) {
        return this._features[id];
    }
    /**
     * 
     * @param {Feature} feature - 
     */
    addFeature(feature, last = true) {
        this._features[feature.id] = feature;
        if (!this._first) {
            this._first = feature;
            this._last = feature;
        }
        else {
            if (!last) {
                this._first.prev = feature;
                feature.next = this._first;
                this._first = feature;
            }
            else {
                this._last.next = feature;
                feature.prev = this._last;
                this._last = feature;
            }
        }
    }
    /**
     * 
     * @param {Feature} feature - 
     */
    removeFeature(feature) {
        if (this._first == feature) {
            this._first = feature.next;
        }
        if (this._last == feature) {
            this._last = feature.prev;
        }
        if (feature.prev) {
            feature.prev.next = feature.next;
        }
        if (feature.next) {
            feature.next.prev = feature.prev;
        }
        feature.prev = null;
        feature.next = null;
        delete this._features[feature.id];
    }
    /**
     * 
     */
    clearFeatures() {
        this._features = {};
    }
    /**
     * 
     * @param {Field} field - 
     */
    addField(field) {
        this._fields.push(field);
    }
    /**
     * 
     * @param {Field} field - 
     */
    removeField(field) {
        const index = this._fields.findIndex(item => item === field);
        index != -1 && this._fields.splice(index, 1);
    }
    /**
     * 
     */
    clearFields() {
        this._fields = [];
    }
    /**
     * 
     * @param {Adapter} adapter - 
     */
    async load(adapter) {
        if (this._type == adapter.type) {
            const features = await adapter.fetch();
            features.forEach(feature => {
                this.addFeature(feature);
            });
        }
    }
}
